.ball{
  display: inline-block;
  border: 1px solid black;
  border-radius: 20px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  text-align: center;
  margin-right: 20px;
}
#screen {
  width: 300px;
  height: 200px;
  text-align: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
#screen.waiting {
  background-color: aqua;
}
#screen.ready {
  background-color: red;
}
#screen.start {
  background-color: greenyellow;
}
#computer{
  width: 142px;
  height: 200px;
  background-position: 0 0;
}
table{
  border-collapse: collapse;
}
td{
  border: 1px solid black;
  width: 40px;
  height: 40px;
  text-align: center;
}
*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}
:root
{
  --clr:lightgrey;
}
body
{
  display: flex;
  justify-content: center;
  /* align-items: top; */
  min-height: 100vh;
  background: lightgrey;
  background: var(--clr);
}
.navigation
{
  position: relative;
  margin-top: 40px;
  width: 400px;
  height: 80px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}
.navigation ul
{
  display: flex;
  width: 360px;
}
.navigation ul li
{
  position: relative;
  list-style: none;
  width: 70px;
  height: 55px;
  z-index: 100;
}
.navigation ul li .nl
{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  text-align: center;
  font-weight: 500;
}
.navigation ul li .nl .icon
{
  position: relative;
  display: block;
  line-height: 75px;
  font-size: 1.5em;
  text-align: center;
  transition: 0.5s;
  color: black;
}
.navigation ul li:hover .nl .icon
{
  -webkit-transform: translateY(-42px);
          transform: translateY(-42px);
}
.navigation ul li.active .nl .icon
{
  -webkit-transform: translateY(-42px);
          transform: translateY(-42px);
}
.navigation ul li .nl .text
{
  position: absolute;
  color: black;
  font-weight: 600;
  font-size: 0.75em;
  letter-spacing: 0.05em;
  transition: 0.5s;
  opacity: 0;
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
}
.navigation ul li:hover .nl .text
{
  opacity: 1;
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}
.navigation ul li.active .nl .text
{
  opacity: 1;
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}
.indicator
{
  position: absolute;
  top: -40%;
  width: 60px;
  height: 60px;
  background: #29fd53;
  border-radius: 50%;
  border: 6px solid lightgrey;
  border: 6px solid var(--clr);
  transition: 0.5s;
}
.indicator::before
{
  content: '';
  position: absolute;
  top: 54%;
  left: -22px;
  width: 20px;
  height: 20px;
  background: transparent;
  border-top-right-radius: 15px;
  box-shadow: 0px -10px 0 0 lightgrey;
  box-shadow: 0px -10px 0 0 var(--clr);
}
.indicator::after
{
  content: '';
  position: absolute;
  top: 54%;
  right: -22px;
  width: 20px;
  height: 20px;
  background: transparent;
  border-top-left-radius: 15px;
  box-shadow: 0px -10px 0 0 lightgrey;
  box-shadow: 0px -10px 0 0 var(--clr);
}
.navigation ul li:nth-child(1).active~.indicator
{
  -webkit-transform: translateX(calc(60px*0));
          transform: translateX(calc(60px*0));
}
.navigation ul li:nth-child(2).active~.indicator
{
  -webkit-transform: translateX(calc(60px*1));
          transform: translateX(calc(60px*1));
}
.navigation ul li:nth-child(3).active~.indicator
{
  -webkit-transform: translateX(calc(60px*2));
          transform: translateX(calc(60px*2));
}
.navigation ul li:nth-child(4).active~.indicator
{
  -webkit-transform: translateX(calc(60px*3));
          transform: translateX(calc(60px*3));
}
.navigation ul li:nth-child(5).active~.indicator
{
  -webkit-transform: translateX(calc(60px*4));
          transform: translateX(calc(60px*4));
}
.navigation ul li:nth-child(6).active~.indicator
{
  -webkit-transform: translateX(calc(60px*5));
          transform: translateX(calc(60px*5));
}
